const totalPrice = computed(() => {
  if (!cartList.value?.length) return 0
  return cartList.value.reduce((sum, item) => {
    const price = Number(item.price) || 0
    const count = Number(item.count) || 0
    return sum + (price * count)
  }, 0)
})

