  //       title: 'ËØ∑ËæìÂÖ•ÂèñÈ§ê‰∫∫ÂßìÂêç',
  //       icon: 'none'
  //     })
  //     return
  //   }
  //   if (!customerPhone.value) {
  //     uni.showToast({
  //       title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑',
  //       icon: 'none'
  //     })
  //     return
  //   }
  //   if (!/^1[3-9]\d{9}$/.test(customerPhone.value)) {
  //     uni.showToast({
